@import url('https://fonts.googleapis.com/css2?family=lato&display=swap');

.App {
  text-align: center;
  position: relative; /* add this */
  min-height: 1000px;
  background-color: #282c34;
  background: url('/Users/anavilla/Desktop/USB/react/src/img/wallpaper.webp') no-repeat center center/cover;
  opacity: 0.8; /* adjust the opacity value to your liking */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.home, .products{
  display: flex;
  height: 90vh;
  align-self: center;
  justify-content: center;
  font-size: 3rem;
}

h2{
  font-size: 2rem !important;
  font-weight: bold !important;
  letter-spacing: 0.5rem;
  color: #fff;
  text-align: left;
}